.page-footer {
  background-color: $color-back;
  display: flex;
  width: 320px;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;
}

.page-footer__logo {
  display: flex;
  justify-content: center;
  padding-top: 35px;
  padding-bottom: 35px;
}

.page-footer__wrapper {
  width: 280px;
}

.page-footer__social {
  display: flex;
  width: 110px;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  padding-top: 25px;
  padding-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
  list-style: none;

  a {
    color: black;
  }
}

.page-footer__copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 25px;
}

@media (min-width: $tablet-width) {
  .page-footer {
  width: 768px;
  }

  .page-footer__wrapper {
    min-width: 708px;
    display: flex;
    align-items: center;
  }

  .page-footer__copyright {
    padding: 0;
  }
}

@media (min-width: $desktop-width) {
  .page-footer {
    min-width: 1440px;
  }

  .page-footer__wrapper {
    width: 1220px;
  }

}
